#root{
    height: 100vh;
    width: 100%;
}

.offcanvas.offcanvas-end{
    border-left: none;
}
.btn-close {
    float: right;
}

.topbar .dropdown-toggle::after {
    content: none;
}

.topbar .dropdown-menu.collaborator_dropdown{
    inset: 11px 0px auto auto !important;
}


.topbar .dropdown-menu {
    inset: 5px 0px auto auto !important;
}

:not(.btn-check)+.btn.pro_head_icon:hover {
    background-color: #fff;
    border-color: #fff;
}

.offcanvas-backdrop {
    z-index: 1040;
    opacity: 1;
    background-color: rgba(0, 0, 0, 0.3);
}
.offcanvas-backdrop.show {
    opacity: 1;
}

@media (max-width: 991px){
.header-menu-mobile .menu-submenu.menu-submenu-classic {
    display: block;
    box-shadow: none;
    position: relative !important;
    inset: auto !important;
    transform: none !important;
}
}

@media (min-width: 992px){
.topbar .dropdown.show .btn.btn-clean, .dropdown.show .btn.btn-clean:focus:not(.btn-text), 
.topbar .dropdown.show .btn.btn-clean:hover:not(.btn-text):not(:disabled):not(.disabled) {
    background-color: #14191f;
    border-color: transparent;
}
.header-menu .menu-nav {
    height: 100%;
}

}
@media screen and (max-width: 767px){
.mega_menu_inner {
    overflow-y: auto;
}
}