.commanBtn{ 
	background: #c83b77; /* Old browsers */
	background: -moz-linear-gradient(45deg, #c83b77 0%, #e06a73 100%); /* FF3.6-15 */
	background: -webkit-linear-gradient(45deg, #c83b77 0%,#e06a73 100%); /* Chrome10-25,Safari5.1-6 */
	background: linear-gradient(45deg, #c83b77 0%,#e06a73 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#c83b77', endColorstr='#e06a73',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */ 
 color:#fff; font: 400 14px/18px 'open sans',sans-serif; margin-right:3px; display:inline-block; vertical-align:middle; transition:all ease 0.3s; -moz-transition:all ease 0.3s; -webkit-transition:all ease 0.3s; cursor:pointer;
  border-radius: 7px; -webkit-border-radius: 7px; -moz-border-radius: 7px;  text-decoration:none;  text-transform:uppercase;
 }
.commanBtn:hover, .commanBtn:focus{
	background: #e06a73; /* Old browsers */
	background: -moz-linear-gradient(left, #e06a73 17%, #c83b77 90%); /* FF3.6-15 */
	background: -webkit-linear-gradient(left, #e06a73 17%,#c83b77 90%); /* Chrome10-25,Safari5.1-6 */
	background: linear-gradient(to right, #e06a73 17%,#c83b77 90%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#e06a73', endColorstr='#c83b77',GradientType=1 ); /* IE6-9 */
	color:#fff; text-decoration:none;
} 
.commanBtn.btn-sm{ padding:8px 12px;}
.commanBtn.btn-md{ padding:13px 25px; font: 400 14px/18px 'open sans',sans-serif;} 
